* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Lora', serif;
	font-size: 30px; }
body {
	font-family: 'Bebas';
	width: 100%;
	a {
		text-decoration: none;
		color: black;
		&:visited {
			color: black; } }
	p {
		span {
			color: orange; } }
	.nextPage,.prevPage {
		display: flex;
		flex-direction: row;
		a {
			display: flex;
			justify-content: center;
			align-items: center;
			color: #009AD0;
			img {
				height: 15px;
				width: auto; } } } }
